import axios from 'axios';
import { Dispatch } from "redux";
import {INews} from "../reducers/news";

export const GET_NEWS_REQUEST = "GET_NEWS_REQUEST";
export const GET_NEWS_SUCCESS = "GET_NEWS_SUCCESS";
export const GET_NEWS_FAIL = "GET_NEWS_FAIL";

const addNewsRequest = ():addNewssRequestType => ({
    type: GET_NEWS_REQUEST,
});
type addNewssRequestType = {
    type: typeof GET_NEWS_REQUEST
}

export const addNewsSuccess = (news: Array<INews>):addNewsSuccessType => ({
    type: GET_NEWS_SUCCESS,
    payload: news
});
type addNewsSuccessType = {
    type: typeof GET_NEWS_SUCCESS
    payload: Array<INews>
}

const addNewsError = (err: string | null): addNewsErrorType => ({
    type: GET_NEWS_FAIL,
    payload: { err }
});
type addNewsErrorType = {
    type: typeof GET_NEWS_FAIL
    payload: { err: string | null }
}

export type NewsActionsType = addNewssRequestType | addNewsSuccessType | addNewsErrorType;

export const getNews = () => {
    return (dispatch:Dispatch<NewsActionsType>) => {
        dispatch(addNewsRequest());

        axios({
            method: 'get',
            url: `https://newsapi.org/v2/top-headlines?country=no&apiKey=60ea0e2e8e454d83a839631a483743dc`,
        })
            .then(response => {
                dispatch(addNewsSuccess(response.data.articles))
            })
            .catch(err => {
                dispatch(addNewsError(err))
            })
    }
}



