import {ActionsTypeNews,
    LOADED_FINISH,
    LOADING_FILMS,
    LOADING_PAGE,
    REQUEST_FILMS,
} from "../actions/FilmsActions";

const initialState = {
    loading: false,
    films: [],
    page: 0,
}

type initialStateType = typeof initialState

export const filmsReduser = (state = initialState, action: ActionsTypeNews):initialStateType => {
    switch (action.type) {
        case REQUEST_FILMS:
            return {
                ...state,
                loading: true
        };

        case LOADING_FILMS:
            return {
                ...state,
                films: action.payload,
                loading: false
            };

        case LOADING_PAGE:
            return {
                ...state,
                page: action.payload,
                loading: false
            };

        case LOADED_FINISH:
            return {
                ...state,
                loading: false
            };

        default:
            return state
    }
}