import axios from 'axios';
import {Dispatch} from "redux";
import {IPhotos} from "../reducers/photos";

export const GET_PHOTOS_REQUEST = "GET_PHOTOS_REQUEST";
export const GET_PHOTOS_SUCCESS = "GET_PHOTOS_SUCCESS";
export const GET_PHOTOS_FAIL = "GET_PHOTOS_FAIL";

const addPhotosRequest = ():addPhotosRequestType => ({
    type: GET_PHOTOS_REQUEST,
});
type addPhotosRequestType = {type: typeof GET_PHOTOS_REQUEST}

export const addPhotosSuccess = (photos:Array<IPhotos>):addPhotosSuccessType => ({
    type: GET_PHOTOS_SUCCESS,
    payload: photos
});
type addPhotosSuccessType = {
    type: typeof GET_PHOTOS_SUCCESS
    payload: Array<IPhotos>
}

const addPhotosError = (err: string | null):addPhotosErrorType => ({
    type: GET_PHOTOS_FAIL,
    payload: { err }
});
type addPhotosErrorType = {
    type: typeof GET_PHOTOS_FAIL
    payload: { err: string | null }
}

export type PhotosActionType = addPhotosRequestType | addPhotosSuccessType | addPhotosErrorType

export const getPhotos = () => {
    return (dispatch: Dispatch<PhotosActionType>) => {
        dispatch(addPhotosRequest());

        axios({
            method: 'get',
            url: `https://api.pexels.com/v1/search?query=nature&per_page=80"`,
            headers: {Authorization: "563492ad6f9170000100000190b900612c0e41a281be655afc75973d"}
        })
            .then(response => {
                dispatch(addPhotosSuccess(response.data.photos))
            })
            .catch(err => {
                dispatch(addPhotosError(err))
            })
    }
}


