import React from 'react';
import {connect, ConnectedProps} from 'react-redux';
import { getPhotos } from '../../actions/PhotosActions';
import { withStyles, createStyles, WithStyles } from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import {compose} from "redux";
import {AppRootStore} from "../../reducers";
import {IPhotos} from "../../reducers/photos";

const styles = () => createStyles({
    blockPictures: {
        display: "flex",
        flexDirection: "row",
        alignItems: "center",
        flexWrap: "wrap",
        justifyContent: "center",
        margin:0,
        padding:0
    },
    blockPicturesItem: {
        listStyle: "none",
        padding: "10px 20px",
        position: "relative",
    },
    picturesItemNote: {
        bottom: "10px",
        left: "20px",
        height: "20px",
        padding: "10px",
        color: "white",
        position: "absolute",
        background: "linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)"
    },
    icon: {
        position: "absolute",
        color: "white",
        top: "10px",
        right: "20px"
    },
});

type Props = ReduxProps & WithStyles<typeof styles> ;

class Album extends React.Component<Props> {
    componentDidMount():void {
        this.props.getPhotos();
    }

    render(): React.ReactNode {
        const { photos, classes } = this.props;

        return (
            <div>
                <ul className={classes.blockPictures}>
                    {
                        photos.length && (photos as Array<IPhotos>).map((el:IPhotos) => {
                            return (
                                <li key={el.id} className={classes.blockPicturesItem}>
                                    <img src={el.src.medium} alt="" key={el.id} height="200px" width="250px"/>
                                    <IconButton className={classes.icon}>
                                        <StarBorderIcon/>
                                    </IconButton>
                                    <span className={classes.picturesItemNote}>{el.photographer}</span>
                                </li>
                            )
                        })
                    }
                </ul>
            </div>
        )
    }
}

const connector = connect((store:AppRootStore)=>({
    photos: store.photosBlock.photos,
}),{
    getPhotos: getPhotos
})

type ReduxProps = ConnectedProps<typeof connector>

export default compose(
    connector,
    withStyles(styles)
)(Album);



