import React, {useState} from 'react';

import {withRouter, NavLink, RouteComponentProps} from 'react-router-dom';
import {IconButton, Toolbar, AppBar, createStyles} from '@material-ui/core/';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import MenuIcon from '@material-ui/icons/Menu';
import {makeStyles} from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import PhotoCameraIcon from '@material-ui/icons/PhotoCamera';
import PublicIcon from '@material-ui/icons/Public';
import Typography from '@material-ui/core/Typography';
import MovieIcon from '@material-ui/icons/Movie';
import HomeIcon from '@material-ui/icons/Home';

const drawerWidth = 240;
const iOS = process.browser && /iPad|iPhone|iPod/.test(navigator.userAgent);

const useStyles = makeStyles((theme)=> createStyles({
    root: {
        display: 'flex',
    },
    title: {
        flexGrow: 1,
    },
    drawer: {
        [theme.breakpoints.up('sm')]: {
            width: drawerWidth,
            flexShrink: 0,
        },
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    toolbar: theme.mixins.toolbar,
    drawerPaper: {
        width: drawerWidth,
    },
    content: {
        flexGrow: 1,
        padding: theme.spacing(3),
    },
    abbBar: {
        backgroundColor: "#85b6b88f"
    },
    link: {
        textDecoration: 'none',
        color: "black"
    }
}));

interface OwnProps {
    title: string
}

interface PropsType {
    children: JSX.Element
}

type Props = OwnProps & PropsType & RouteComponentProps;

const Layout: React.FC<Props> = ({title, children}) => {
    const classes: any = useStyles();

    const [mobileOpen, setMobileOpen] = useState<boolean>(false);

    const handleDrawerToggle = (): void => {
        setMobileOpen(!mobileOpen);
    };

    return (
        <div className={classes.root}>
            <AppBar position="fixed" className={classes.abbBar}>
                <Toolbar>
                    <Typography variant="h6" className={classes.title}>
                        {title}
                    </Typography>
                    <span style={{flexGrow: 1}}></span>
                    <IconButton
                        color="inherit"
                        edge="end"
                        onClick={handleDrawerToggle}
                        className={classes.menuButton}
                    >
                        <MenuIcon/>
                    </IconButton>
                </Toolbar>
            </AppBar>
            <div>
                <SwipeableDrawer
                    anchor={'left'}
                    open={mobileOpen}
                    onClose={handleDrawerToggle}
                    onOpen={handleDrawerToggle}
                    disableBackdropTransition={!iOS}
                    disableDiscovery={iOS}
                >
                    <div
                        role="presentation"
                        onClick={handleDrawerToggle}
                        onKeyDown={handleDrawerToggle}
                    >
                        <List>
                            <NavLink to="/news" key="news" className={classes.link}>
                                <ListItem button>
                                    <ListItemIcon><PublicIcon/></ListItemIcon>
                                    <ListItemText primary="News"/>
                                </ListItem>
                            </NavLink>
                            <NavLink to="/album" key="album" className={classes.link}>
                                <ListItem button>
                                    <ListItemIcon><PhotoCameraIcon/></ListItemIcon>
                                    <ListItemText primary="Album"/>
                                </ListItem>
                            </NavLink>
                            <NavLink to="/films" key="films" className={classes.link}>
                                <ListItem button>
                                    <ListItemIcon><MovieIcon/></ListItemIcon>
                                    <ListItemText primary="Films"/>
                                </ListItem>
                            </NavLink>
                            <NavLink to="/" key="Home" className={classes.link}>
                                <ListItem button>
                                    <ListItemIcon><HomeIcon/></ListItemIcon>
                                    <ListItemText primary="Home"/>
                                </ListItem>
                            </NavLink>
                        </List>
                    </div>
                </SwipeableDrawer>
            </div>
            <main className={classes.content}>
                <div className={classes.toolbar}/>
                {children}
            </main>
        </div>
    );
}

export const MainLayout = withRouter(Layout);

