import {GET_PHOTOS_REQUEST, GET_PHOTOS_SUCCESS, GET_PHOTOS_FAIL, PhotosActionType} from '../actions/PhotosActions';

 export interface IPhotos {
    avg_color: string
    height: number
    id: number
    liked: boolean
    photographer: string
    photographer_id: number
    photographer_url: string
    src: {
        landscape: string
        large: string
        large2x: string
        medium: string
        original: string
        portrait: string
        small: string
        tiny: string
    }
    url: string
    width: number
}

type PhotosStateType = {
    photos: Array<IPhotos>
    loading: boolean
    error: string | null
}

const initialState: PhotosStateType = {
    photos: [],
    loading: false,
    error: null
}

export function photosReducer(state = initialState, action:PhotosActionType): PhotosStateType{
    switch (action.type) {

        case GET_PHOTOS_REQUEST:
            return { 
                ...state, 
                loading:true
            }

        case GET_PHOTOS_SUCCESS:
            return { 
                ...state, 
                loading: false,
                photos: action.payload
            }

        case GET_PHOTOS_FAIL:
            return { 
                ...state, 
                error: action.payload.err, 
                loading: false 
            }

        default:
            return state
    }
}