import {Dispatch} from "react";
import axios from "axios";

export const REQUEST_FILMS = 'REQUEST_FILMS';
export const LOADING_FILMS = 'LOADING_FILMS';
export const LOADED_FINISH = 'LOADED_FINISH';

export const LOADING_PAGE = 'LOADING_PAGE';

const requestFilms = ():requestFilmsType => ({
    type: REQUEST_FILMS
})
type requestFilmsType = {type: typeof REQUEST_FILMS}

export const loadingFilms = (films:any):loadingFilmsType => ({
    type: LOADING_FILMS,
    payload: films
})
type loadingFilmsType = {type: typeof LOADING_FILMS, payload:any}

const requestFinished = ():requestFinishedType => ({
    type: LOADED_FINISH,
})
type requestFinishedType = {type: typeof LOADED_FINISH}

const loadingPage = (page:number):loadingPageType => ({
    type: LOADING_PAGE,
    payload: page
})
type loadingPageType = {type: typeof LOADING_PAGE, payload:number}

export type ActionsTypeNews = requestFilmsType | loadingFilmsType | requestFinishedType | loadingPageType;

export const getFilms = (params: any) => {
    return (dispatch: Dispatch<ActionsTypeNews>)=> {
        dispatch(requestFilms())

        axios({
            method: 'get',
            url: `https://rickandmortyapi.com/api/character/?page=${params.page}`,
        })
            .then(response => {
                dispatch(loadingFilms(response.data.results))
                dispatch(loadingPage(response.data.info.pages))
            })
            .catch(err => {
                dispatch(requestFinished())
            })
    }

}

