import React from 'react'
import {Box, Typography, withStyles, createStyles, WithStyles} from "@material-ui/core";
import {connect, ConnectedProps} from "react-redux";
import {compose} from "redux";
import {AppRootStore} from "../../reducers";
import {getFilms} from "../../actions/FilmsActions";
import Pagination from '@material-ui/lab/Pagination';

const styles = () => createStyles({
    title: {
        color: "cadetblue"
    },
    block: {
        display:'flex',
        alignItems:'center',
        flexWrap: 'wrap',
        justifyContent: 'center'
    },
    blockItem: {
        margin:'15px',
        padding:'0 20px 0 0',
        backgroundColor:'#56d5e62b',

        width: '400px',
        height:'200px',
        display:'flex',
        justifyContent: 'space-between',
        borderRadius:'15px',
    },

    itemImg: {
        borderRadius:'15px 0 0 15px'
    },
    blockInfo: {
        padding: '10px 20px',
        textAlign: 'center',
        width: '200px'
    },
    blockText: {
        fontWeight:600,
        fontSize:'18px'
    },
    mainBlock: {
        display:'flex',
        flexDirection:'column',
        justifyContent:'center',
        alignItems:'center'
    }

})

interface IFilms {
    created: string
    episode: Array<string>
    gender: string
    id: number
    image: string
    location: {name: string}
    name: string
    origin: {name: string, url: string}
    species: string
    status: string
    type: string
    url: string
}

type StateType = {
    page: number
}

type Props = ReduxProps & WithStyles<typeof styles>;

class Films extends React.Component<Props, StateType>{
    constructor(props:Props) {
        super(props);
        this.state = {
            page: 1,
        }
    }

    componentDidMount(): void {
        const params = {
            page:this.state.page
        }
        this.props.getFilms(params)
    }

    loadNextPage=(e:React.ChangeEvent<unknown>, page:number)=>{
        this.setState((prevState)=> ({
            page: prevState.page
        }));
        const params = {
            page: page
        }
        this.props.getFilms(params)
    }

    render(): React.ReactNode  {
        const { classes, films, page } = this.props;
        return (
            <div className={classes.mainBlock}>
                <Typography variant="h3" align="center" className={classes.title}>Some information about films!</Typography>

                <div className={classes.block}>
                {
                    films.map((item:IFilms)=>{
                        return (
                            <div key={item.name} className={classes.blockItem}>
                                <Box>
                                    <img src={item.image}  className={classes.itemImg} width={"200px"} height={"200px"} />
                                </Box>
                                <Box className={classes.blockInfo}>
                                    <Typography variant="body1" className={classes.blockText}>{item.gender} - {item.type}</Typography>
                                    <Typography variant="body1" className={classes.blockText}>{item.name}</Typography>
                                    <Typography variant="body1" className={classes.blockText}>{item.status}</Typography>
                                </Box>
                            </div>
                        )
                    })
                }
            </div>
                <Pagination count={page} onChange={this.loadNextPage}/>
            </div>
        );
    }
}

const Connector = connect(
    (store:AppRootStore)=>({
        films: store.filmsBlock.films,
        page: store.filmsBlock.page
    }), {
        getFilms: getFilms
    });

type ReduxProps = ConnectedProps<typeof Connector>

export default compose(
    Connector,
    withStyles(styles)
)(Films)
