import {GET_NEWS_REQUEST, GET_NEWS_SUCCESS, GET_NEWS_FAIL, NewsActionsType} from '../actions/NewsActions';

export interface INews {
    author: string
    content: string
    description: string
    publishedAt: string
    source: {
        id: number
        name: string
    }
    title: string
    url: string
    urlToImage: string
}

export type NewStateType = {
    news: Array<INews>
    loading: boolean
    error: string | null
}

const initialState: NewStateType = {
    news:[],
    loading: false,
    error: null
}

export function newsReducer(state = initialState, action: NewsActionsType):NewStateType{
    switch (action.type) {

        case GET_NEWS_REQUEST:
            return { 
                ...state, 
                loading:true
            }

        case GET_NEWS_SUCCESS:
            return { 
                ...state, 
                loading: false,
                news: action.payload
            }

        case GET_NEWS_FAIL:
            return { 
                ...state, 
                error: action.payload.err, 
                loading: false 
            }
        default:
            return state
    }
}