import React from 'react';
import {TextField, withStyles, createStyles, WithStyles} from "@material-ui/core";
import back  from './../../images/back.png';
import Button from '@material-ui/core/Button';
import { withRouter } from "react-router";

const styles = () => createStyles({
    root: {
        color: "cadetblue",
        display:'flex',
        justifyContent:'center',
        marginTop: '200px',

        position: 'absolute',
        top: '5%',
        right: '35%'
    },
    field:{
        display:'flex',
        flexDirection:'column',
        justifyContent:'center',
        alignItems:'center',
        width:'300px',
        padding:'20px 30px 50px 30px',
        border: '10px solid transparent',
        borderImage: ' 10 repeating-linear-gradient(45deg, #A7CECC, #A7CECC 10px, transparent 10px, transparent 20px, #F8463F 20px, #F8463F 30px,transparent 30px, transparent 40px);',
        borderImageSlice: 1,
    },
    textField: {
        marginTop: '10px'
    },
    btn: {
        marginTop: '10px'
    },
    btnGroup: {
        display:'flex',
        justifyContent: 'space-between',
        width: '170px'
    }
})

interface IProps {
    match: any,
    location: any,
    history: any
}

type StateType = {
    errorFieldLogin: boolean,
    errorFieldPassword: boolean,
    Login: string,
    Password: string,
}

type Props = WithStyles<typeof styles> & IProps;

class Login extends React.Component<Props, StateType>{
    constructor(props: Props) {
        super(props);
        this.state = {
            errorFieldLogin: false,
            errorFieldPassword: false,
            Login: localStorage.getItem('Login') || '',
            Password: localStorage.getItem('Password') || '',
        }
    }

    onChangeFiled=(name:string, e:React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>):void=>{
        localStorage.setItem(name, e.target.value);

        this.setState({
            [name]: e.target.value,
            [`errorField${name}`]: false,
        } as any)
    }

    onBlurField=(name:string):void=>{
        const { Login, Password} = this.state;
        if (name === 'Login') {
            let reg = /^([aA-zZ]+)$/i
            if (!reg.test(Login)) {
                this.setState({
                    errorFieldLogin: true
                })
            }
        } else if (name === 'Password') {
            if (Password.length <= 3) {
                this.setState({
                    errorFieldPassword: true
                })
            }
        }
    }

    handleSubmitForm=():void=>{
        const { Login, Password, errorFieldLogin, errorFieldPassword} = this.state;

        if (!Login.length){
            this.setState({
                errorFieldLogin: true
            })
        } else if (!Password.length) {
            this.setState({
                errorFieldPassword: true
            })
        } else if (!errorFieldLogin && !errorFieldPassword){
            this.props.history.push('/news')
        }
    }

    clearData=():void=>{
        localStorage.clear();
        this.setState({
            Login:'',
            Password:''
        })
    }

    render(): React.ReactNode {
        const { errorFieldLogin, errorFieldPassword, Login, Password } = this.state;
        const {classes} = this.props

        return (
            <div className="wrapp">
                <img src={back} width="100%" style={{position:'relative'}}/>
                <form className={classes.root} onSubmit={this.handleSubmitForm} autoComplete="off">
                    <div className={classes.field}>
                        <TextField
                            label="Login"
                            error={errorFieldLogin}
                            onChange={(e)=>this.onChangeFiled("Login", e)}
                            onBlur={(e)=>this.onBlurField("Login")}
                            value={Login}
                            id="standard-error"
                            helperText={errorFieldLogin && "only english letters"}
                            className={classes.textField}
                        />
                        <TextField
                            label="Password"
                            type="password"
                            error={errorFieldPassword}
                            value={Password}
                            onChange={(e)=>this.onChangeFiled("Password", e)}
                            onBlur={(e)=>this.onBlurField("Password")}
                            helperText={errorFieldPassword && "at least 4 characters"}
                            id="standard-error-helper-text"
                            className={classes.textField}
                        />
                        <div className={classes.btnGroup}>
                            <Button
                                variant="contained"
                                color="primary"
                                className={classes.btn}
                                type="submit"
                            >
                                Log in
                            </Button>
                            <Button
                                variant="contained"
                                color="primary"
                                className={classes.btn}
                                onClick={this.clearData}
                            >
                                Clear
                            </Button>
                        </div>


                    </div>
                </form>
            </div>
        )
    }
}

const LoginWrapp = withRouter(Login)
export default withStyles(styles)(LoginWrapp);