import React from 'react'
import { Route } from 'react-router'
import {MainLayout} from './Layout'

interface Props {
    layout?: React.ComponentType;
    component: any;
    path: string;
    exact?: boolean;
    sensitive?: boolean;
    strict?: boolean;
    title?: any;
}

export const Page: React.FC<Props> = ({
    layout: Layout = MainLayout,
    component: Component,
    title,
    ...rest
}) => {
    return (
        <Route
            {...rest}
            render={props => (
                title === 'Login' ? (
                    <Component {...props} />
                ) : (
                    <Layout title={title}>
                        <Component {...props} />
                    </Layout>
                )

            )}
        />
    )
}
